import React from "react";
import styled from "styled-components";

const Atag = styled.a`
  cursor: pointer;
  text-decoration: underline;
  &:hover {
    transition: 0.5s ease-in-out;
    font-weight: 400;
    color: ${(props) => props.themeColor};
  }
`;

const Btag = styled.span`
  font-weight: 400;
  display: flex;
  margin-top: 10px;
  margin-bottom: 10px;
  color: ${(props) => props.themeColor};
`;

const Block = styled.div`
  display: flex;
  width: 100%;
  height: 10px;
`;

const Minus = styled.div`
  font-size: 16px;
  color: rgba(0, 0, 0, 0.1);
  margin-top: 8px;
  margin-bottom: 14px;
`;

const OL = ({
  url,
  children,
  themeColor = "rgba(0,0,0,1)",
  target = "_blank",
}) => {
  return (
    <Atag target={target} href={url} themeColor={themeColor}>
      {children}
    </Atag>
  );
};

const BT = ({ children, themeColor = "rgba(0,0,0,1)" }) => {
  return <Btag themeColor={themeColor}>{children}</Btag>;
};

const BL = () => {
  return <Block></Block>;
};

const HB = () => {
  return <Minus className="dD">-</Minus>;
};

export { OL, BT, BL, HB };
