import React from "react";
import styled from "styled-components";
import { OL } from "./TextDeco";

const Box = styled.div`
  width: 100%;
  background-color: ${(props) =>
    props.end === "true" ? "rgba(0, 0, 0, 1)" : "rgba(255, 255, 255, 1)"};
  border: ${(props) =>
    props.end === "true" ? "none" : "1px solid rgba(0, 0, 0, 0.1)"};
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  margin-bottom: 20px;
`;

const Title = styled.div`
  width: 100%;
  font-family: "Outfit", sans-serif;
  font-size: 16px;
  margin-bottom: 13px;
  color: ${(props) =>
    props.end === "true" ? "rgba(255, 255, 255, 1)" : "rgba(0, 0, 0, 1)"};
`;

const Desc = styled.div`
  margin-bottom: 7px;
  width: 100%;
  font-size: 12px;
  color: ${(props) =>
    props.end === "true" ? "rgba(255, 255, 255, 0.5)" : "rgba(0, 0, 0, 0.5)"};
  :last-child {
    margin-bottom: 0px;
  }
`;

const Outro = ({ end, date }) => {
  return (
    <Box end={end} className="dD">
      <Title end={end}>{end === "true" ? "The End" : "To Be Continued"}</Title>
      <Desc end={end}>
        {end === "true" ? `${date}에 찍힌 마침표` : `${date}에 찍힌 쉼표`}
      </Desc>
      {end === "true" ? (
        <Desc end={end}>
          다른{" "}
          <OL
            url={"https://victorlee917.github.io/montage/#/"}
            target={"_self"}
            themeColor="white"
          >
            montage.gif
          </OL>
          도 구경해볼까요?
        </Desc>
      ) : (
        <Desc end={end}>
          업데이트 소식은{" "}
          <OL url={"https://www.instagram.com/montage.gif/"}>@montage.gif</OL>
          에서 만나보세요!
        </Desc>
      )}
    </Box>
  );
};

export default Outro;
